.pagename {
  font-size: 24px;
  color: var(--primary-grey);
  margin-bottom: 30px;
  position: relative;
  font-family: geologica;
  top: 0px;
  padding: 2rem;
}

.pagename a {
  color: var(--primary-grey);
  text-decoration: none;
}

.pagename span a {
  color: var(--primary-orange);
}

.maincourse-container {
  display: flex;
  padding: 40px;
  /* width: 1152px; */
  height: 567px;

}

.maincourse-section {
  display: flex;
  width: 1152px;
  height: 567px;
  border: 2px solid #ddd;
  border-radius: 20px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  background: var(--item-color);
  padding: 36px 96px;
  margin-left: 57px;
}


.maincourse-content {
  flex: 1;
  max-width: 50%;

}

.maincourse-heading {
  font-size: 50px;
  font-weight: 400;
  color: var(--primary-orange);
  margin-bottom: 1rem;
  font-family: 'Lilita One';
}

.maincourse-points-wholelist {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.maincourse-points {
  list-style: none;
  padding: 0;
  margin: 0;
}

.maincourse-points-list {
  display: flex;
  flex-direction: row;
  font-size: 22px;
  color: var(--primary-grey);
  font-family: Geologica;
  gap: 10px;
}

.maincourse-subcontent li {
  font-size: 24px;
  font-family: Geologica;
  color: var(--primary-grey);
  line-height: 50px;

}

.maincourse-subcontent li span {
  font-weight: 400;

}

.maincourse-points p {
  display: flex;
  font-size: 18px;
  color: var(--primary-grey);
  font-family: Geologica;
  margin: 8px 0;
}

.maincourse-points p img {
  width: 20px;
  height: 20px;
}

.maincourse-points-list span {
  font-weight: 400;
  font-size: 24px;
  font-family: geologica;
  color: var(--primary-grey);
}

.maincourse-subcontent span {
  font-size: 24px;
  font-family: geologica;
  color: var(--primary-orange);
}

.maincourse-button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1.5rem;
  width: 389px;
  height: 56px;
  background-color: var(--primary-orange);
  color: var(--bg-color);
  font-size: 24px;
  font-weight: semibold;
  font-family: geologica;
  text-align: center;
  border-radius: 8px;
  text-decoration: none;
  box-shadow: 0 4px 8px rgba(245, 134, 52, 0.4);
  transition: all 0.3s ease;
}

.maincourse-button:hover {
  background-color: #e7752a;
  box-shadow: 0 6px 12px rgba(245, 134, 52, 0.6);
}

.maincourse-image {
  flex: 1;
  max-width: 50%;
  display: flex;
  justify-content: flex-end;
}

.maincourse-image img {
  object-fit: cover;
  transition: transform 0.3s ease;
  animation: none;
  /* No animation initially */
}

.maincourse-image img:hover {
  animation: wiggle 1.0s ease infinite;
  /* Trigger wiggle on hover */
}

@keyframes wiggle {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(-5deg);
  }

  50% {
    transform: rotate(5deg);
  }

  75% {
    transform: rotate(-5deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

@media (max-width: 768px) {
  .maincourse-container {
    flex-direction: column;
    padding: 20px;
  }

  .maincourse-section {
    flex-direction: column-reverse;
    /* Stack image on top of content if desired, or column for standard stacking */
    width: 100%;
    height: auto;
    padding: 20px;
    margin: 0;
    /* Remove negative margins */
    box-sizing: border-box;
  }

  .maincourse-content {
    max-width: 100%;
    text-align: center;
  }

  .maincourse-subcontent li {
    font-size: 17px;
    line-height: 59px;
    margin-top: -9%;
  }

  .maincourse-subcontent span {
    font-size: 15px;
  }

  .pagename {
    font-size: 15px;
    top: 0;
    left: 0;
    padding: 10px 0;
    text-align: center;
    width: 100%;
  }

  .maincourse-heading {
    font-size: 31px;
    margin-bottom: 19px;
    margin-top: 5px;
  }

  .maincourse-points-wholelist {
    line-height: 26%;
  }

  .maincourse-points p img {
    width: 22px;
    height: 21px;
    margin-top: -11px;
    margin-bottom: 15px;
  }

  .maincourse-points-list,
  .maincourse-points-list span {
    font-size: 17px;
    text-wrap: nowrap;
  }

  .maincourse-button {
    width: 83%;
    height: 2%;
    font-size: 16px;
    padding: 15px;
    margin-top: -3%;
  }

  .maincourse-image img {
    width: 100%;
    height: auto;
  }

}

@media (max-width: 426px) {
  .pagename {
    font-size: 14px;
    left: 0;
    top: 0;
    padding: 1rem 0;
  }

  .maincourse-container {
    height: auto;
  }

  .maincourse-section {
    flex-direction: column;
    width: 90%;
    padding: 20px;
  }

  .maincourse-content {
    order: 2;
  }

  .maincourse-image {
    max-width: 95%;
  }

  .maincourse-heading {
    font-size: 30px;
  }

  .maincourse-points-list,
  .maincourse-subcontent li {
    font-size: 15px;
  }

  .maincourse-points-list span,
  .maincourse-subcontent li span {
    font-size: 15px;
  }

  .maincourse-button {
    font-size: 14px;
    padding: 2px;
    width: 100%;
    height: 50px;
  }

  .maincourse-image img {
    width: 100%;
    height: auto;
  }

  .maincourse-image img:hover {
    animation: none;
  }
}

@media (max-width: 376px) {
  .pagename {
    font-size: 14px;
    padding: 1rem 0;
    left: 0;
    top: 0;
  }

  .maincourse-container {
    height: auto;
  }

  .maincourse-section {
    flex-direction: column;
    width: 90%;
    padding: 15px;
  }

  .maincourse-content {
    order: 2;
  }

  .maincourse-image {
    max-width: 120%;
  }

  .maincourse-heading {
    font-size: 26px;
  }

  .maincourse-points-list,
  .maincourse-subcontent li {
    font-size: 14px;
  }

  .maincourse-points-list span,
  .maincourse-subcontent li span {
    font-size: 14px;
    width: 17%;
  }

  .maincourse-button {
    font-size: 12px;
    padding: 4px;
    width: 98%;
  }
}

@media (max-width: 320px) {
  .pagename {
    font-size: 10px;
    padding: 0.5rem 0;
    text-align: center;
    margin-left: 0;
  }

  .maincourse-content {
    width: px;
  }

  .maincourse-section {
    width: 85%;
  }

  .maincourse-image {
    max-width: 90%;
  }

  .maincourse-points-list span,
  .maincourse-subcontent li span {
    width: 26%;
  }
}