.card-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 100%;
  background-color: var(--bg-color);
}

.home-card-info-text {
  text-align: center !important;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.home-card-info-text h2 {
  text-align: center !important;
  font-size: 24px;
  font-weight: 400;
  color: var(--primary-orange);
  margin-bottom: 10px;
  font-family: geologica;
  width: 100%;
  display: block;
}

.home-card-info-text h3 {
  text-align: center !important;
  font-size: 50px;
  color: var(--primary-grey);
  font-weight: 800;
  margin-top: -19px;
  margin-bottom: -19px;
  font-family: lilita one;
  width: 100%;
  display: block;
}

.home-card-info-text p {
  text-align: center !important;
  font-size: 22px;
  margin-top: 20px;
  font-weight: 100;
  font-family: geologica;
  color: var(--primary-grey);
  width: 100%;
  display: block;
}

.Card {
  display: flex;
  flex-direction: row;
  overflow-x: auto;
  scroll-behavior: smooth;
  gap: 28px;
  padding: 10px;
  width: 78%;
  height: 565px;
}

.Card::-webkit-scrollbar {
  display: none;
}


.navigation-buttons {
  display: flex;
  justify-content: center;
  margin-top: -14px;
  margin-left: 965px;
}

.navigation-buttons button {
  background-color: var(--primary-orange);
  border: 1px solid #ccc;
  border-radius: 25%;
  padding: 10px 15px;
  cursor: pointer;
  margin: 0 0px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3);
  transition: background-color 0.3s ease;
}

.navigation-buttons button:hover {
  background-color: #fa8d3f;
}

.navigation-buttons .prev::before,
.navigation-buttons .next::before {
  content: '';
}

