  height: 100px;
}

@media (max-width:321px) {
  .Card {
    gap: 10px;
    padding: 10px;
    width: 99%;
  }

  .product-box1 {
    height: 455px;
    width: 273px;
    background-color: var(--bg-color);
    border-radius: 41px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3);
    transition: box-shadow 0.3s ease-in-out;
  }

  .product-box1 img {
    width: 283px;
    margin-left: -5px;
    height: 278px;
    object-fit: cover;
    margin-bottom: -21px;
  }

  .description-box1 {
    padding: 0px 11px;
  }

  .description-box1 h3 {
    font-size: 15px;
  }

  .info-item2 {
    display: flex;
    gap: 13px;
    font-size: 16.96px;
    color: var(--primary-grey);
    margin-top: -30px;
  }

  .info-item1 {
    margin-left: 104px;
  }

  .navigation-buttons {
    margin-top: -90px;
    margin-left: 212px;
  }
}